import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {Component, Input, OnInit} from '@angular/core';
import {Observable} from 'rxjs';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {environment} from 'src/environments/environment';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {OrderAndOrderDetailResponse} from 'src/app/core/models/seller/order/response/OrderAndOrderDetailResponse';
import {OrderStateResponse} from 'src/app/core/models/seller/order/response/OrderStateResponse';

@Component({
	selector: 'app-order-detail-select-modal',
	templateUrl: './order-detail-select-modal.component.html',
	styleUrls: ['./order-detail-select-modal.component.less'],
	providers: [DestroyService]
})
export class OrderDetailSelectModalComponent implements OnInit {
	@Input() orderDetails$?: Observable<ApiResult<OrderAndOrderDetailResponse>>;
	orderAndOrderDetail: OrderAndOrderDetailResponse;
	isLoading: boolean;
	orderStateResponses?: Array<OrderStateResponse>;
	currentState?: string;
	host: string = environment.apiUrl;
	constructor(private destroy$: DestroyService) {
		this.isLoading = true;
		this.orderAndOrderDetail = {} as OrderAndOrderDetailResponse;
	}

	ngOnInit(): void {
		this.isLoading = true;
		this.orderDetails$.pipe(takeUntil(this.destroy$)).subscribe((result) => {
			if (result.resultObj) {
				console.log(this.orderAndOrderDetail);
				this.orderAndOrderDetail = result.resultObj;
				console.log(this.orderAndOrderDetail);
				this.orderStateResponses = result.resultObj.orderStateResponses;
				this.orderStateResponses.sort((a, b) => {
					if (a.orderStatus && b.orderStatus) {
						return parseInt(a.orderStatus) - parseInt(b.orderStatus);
					} else {
						return 0;
					}
				});
			}

			this.isLoading = false;
		});
	}
}
